<script>
  import { getDisplayTime } from "../services/displayTime.js";
  import { createEventDispatcher } from "svelte";
  import Button, { Label } from "@smui/button";
  import page from "page";

  export let image;
  export let hasDeleteButton;

  let pathName = location.pathname || "home";
  pathName = pathName.indexOf("/") == 0 ? pathName.substring(1) : PathName;

  let landscapeClass = "portrait";

  const dispatch = createEventDispatcher();

  function deleteLocation(e) {
    e.stopPropagation();
    dispatch("delete", image);
  }

  function imageLoaded(e) {
    if (e.target.width > e.target.height) {
      landscapeClass = "landscape";
    }
  }
</script>

<style>.imageContainer{margin:5px;background-color:#ffff65;font-size:18px;border-radius:6px;width:100%}.imageContainer>div{padding:16px}@media (color-index:48){.imageContainer{color:#000}}@media (prefers-color-scheme:dark){.imageContainer{color:#000}}.imageText{width:100%}.imageSubtitle{font-size:12px;text-align:right}.square{position:relative;width:200px;height:200px;overflow:hidden;margin:6px auto}img{position:absolute;max-width:100%;width:100%;height:auto;top:50%;left:50%;transform:translate(-50%,-50%)}img.landscape{height:100%;width:auto;-o-object-fit:cover;object-fit:cover}
/*# sourceMappingURL=src/components/Image.svelte.map */</style>

<div
  class="imageContainer mdc-card"
  on:click={() => page('/slideShow?key=' + image.key + '&backUrl=' + pathName)}>
  <div>
    <div class="imageText">{image.imageTitle}</div>
    <div class="square">
      <img
        src={image.thumbnail}
        class={landscapeClass}
        on:load={imageLoaded}
        alt={image.funimage} />
    </div>
    <div class="imageText">{image.funFact}</div>
    <div class="imageSubtitle">{getDisplayTime(image.insertTime)}</div>
    {#if hasDeleteButton}
      <Button on:click={deleteLocation} variant="raised" class="formButton">
        <Label>Delete location</Label>
      </Button>
    {/if}
  </div>
</div>
