<script>
  import { onMount } from "svelte";
  import { Icon } from "@smui/icon-button";
  import { tick } from "svelte";
  import { subscribeToImages } from "./../services/imagesWrapperService.js";
  import page from "page";
  export let params;
  let map;
  let mapElement;
  let markers = [];
  let online = window.navigator.onLine;

  function initMap() {
    if (!window.googleMapsLoaded) {
      return;
    }
    let initialCoords = { lat: 46.65, lng: 7.709 };

    map = new google.maps.Map(mapElement, {
      center: initialCoords,
      zoom: 3
    });
  }

  function addImageToMap(image) {
    if (markers.some(x => x.key == image.key)) {
      return;
    }

    var marker = new google.maps.Marker({
      position: { lat: image.location.latitude, lng: image.location.longitude },
      title: image.imageTitle,
      map: map,
      animation: google.maps.Animation.DROP
    });
    marker.key = image.key;
    marker.addListener("click", function() {
      createInfoWindow(image, marker);
    });
    marker.addListener("mouseover", function() {
      createInfoWindow(image, marker);
    });
    markers.push(marker);

    if (params.key == image.key) {
      createInfoWindow(image, marker);
    }
  }

  // To add the marker to the map, call setMap();

  async function addImagesToMap(images) {
    while (images.length > 0) {
      let i = 0;
      while (images.length > 0 && i++ < 10) {
        var img = images.pop();
        delayedAdd(img);
      }
    }
  }

  function delayedAdd(img) {
    window.setTimeout(function() {
      addImageToMap(img);
    }, 1000 + Math.random() * 7000);
  }

  onMount(async () => {
    if (online) {
      initMap();
      subscribeToImages().subscribe(addImagesToMap);
    }
  });

  function getTemplate(image) {
    return `<div id="content" class="markerPopUp">
    <h2>${image.imageTitle}</h2>
    <div>
    <img src="${image.thumbnail}" alt="${image.imageTitle}" />
    <p>${image.funFact}</p>
    </div></div>`;
  }
  let infowindow = null;
  function createInfoWindow(image, marker) {
    if (infowindow) {
      infowindow.close();
    }
    infowindow = new google.maps.InfoWindow({
      content: getTemplate(image)
    });
    infowindow.open(map, marker);
    page("/map?key=" + marker.key);
  }
</script>

<style type="text/postcss">.mapContainer{height:calc(100% - 50px);width:100%;position:absolute;left:0}#map{height:100%;width:100%}:global(.markerPopUp){padding:10px;text-align:center;max-width:210px}@media (color-index:48){:global(.gm-style-iw-d){overflow:hidden!important}:global(.gm-style-iw),:global(.gm-style-iw-d){background-color:#000!important}:global(.gm-style-iw-t:after){background:#000!important}}@media (prefers-color-scheme:dark){:global(.gm-style-iw-d){overflow:hidden!important}:global(.gm-style-iw),:global(.gm-style-iw-d){background-color:#000!important}:global(.gm-style-iw-t:after){background:#000!important}}:global(.markerPopUp img){max-height:150px}
/*# sourceMappingURL=src/components/Map.svelte.map */</style>

{#if online}
  <div class="mapContainer">
    <div id="map" bind:this={mapElement} />
  </div>
{:else}
  <h1>Sorry, Google Maps only work when online!</h1>
  Go to
  <a href="/images">Images</a>
  instead.
{/if}
